
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

h1, h2 {
  font-weight: normal;
}
a {
  color: #42b983;
}
.borda {
    background-color: #9ddffd;
    margin: 0 auto;
    margin-bottom: 20px;
    width: 600px;
    padding: 20px;
    border-radius: 12px;
}
ul {
  list-style-type: none;
}
.search-button {
    background-color: #2196F3;
    color: #FFF;
    padding: 15px;
    padding-bottom: 13px;
    border: 0px;
    border-radius: 6px;
    cursor: pointer;
}
.search-button:hover {
  background-color: #00bcd4;
}
.form-input {
    margin-top: 10px;
    margin-bottom: 10px;
    padding: 8px;
    font-size: 20px;
    border-radius: 10px;
    border: 2px solid #9E9E9E;
}
.form-input:focus {
       border-radius: 10px;
       border: 2px solid #9E9E9E;
}
table {
     margin: 0 auto;
     border-collapse: collapse;
}
table, th, td {
     border: 1px solid #000;
     padding: 7px;
}
